/*
* @file axios
*/

import axios from 'axios';

const baseURL = 'https://api.github.com/'

const http = axios.create({
  baseURL,
  headers: {
    'Authorization': 'token ce90c199a1b3f9ad1004f0e03dfdc8a2e4b7f731',
  }
})
// 请求拦截器
http.interceptors.request.use(config => {
  const headers = {
    // 'If-None-Match': localStorage.getItem('GITHUB_API_HEADERS_ETAG') || '',
  }
  config.headers = Object.assign(config.headers, headers)
  // 发送前 处理
  return config
}, error => {
  return Promise.reject(error)
})

// 响应拦截器
http.interceptors.response.use(response => {
  localStorage.setItem('GITHUB_API_HEADERS_ETAG', response.headers.etag)
  return response.data
}, error => {
  return Promise.reject(error)
})

http.install = (Vue) => {
  Vue.prototype.$axios = http;
};

export default http;



